import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

import Main from "./components/main.component";
import Business from "./components/business.component";
import Checker from "./components/checker.component";
import Quarantine from "./components/quarantine.component";
import LeaveFeedback from "./components/leave-feedback.component";

// Stateless functional component
function App() {
  return (
    <Router>
      <br />
      <div className="container">
        {/* <Route path="/" exact component={Business} /> */}
        <Route path="/" exact component={Main} />
        <Route path="/business" component={Business} />
        <Route path="/checker" component={Checker} />
        <Route path="/quarantine" component={Quarantine} />
        <Route path="/leave-feedback" component={LeaveFeedback} />
      </div>
    </Router>
  );
}

export default App;
